@font-face {
    font-family: "Lato";
    src: url("../fonts/Lato-Regular.ttf");
}

@font-face {
    font-family: "Lato-light";
    src: url("../fonts/Lato-Light.ttf");
}

html {
    font-size: $font-size-base;
    line-height: $line-height-base;
}

body,
.p {
    font-size: 1em;
    font-family: $font-lato-light;
    color: $color-text-base;
    //text-decoration: line-through;
}

b,
strong {
    font-family: $font-lato;
}

h1,
.h1 {
    font-family: $font-lato;
    font-size: $font-size-h1;
    @include line-height($line-height-h1);
    font-weight: $font-weight-bold;
}

h2,
.h2 {
    font-family: $font-lato;
    font-size: $font-size-h2;
    @include line-height( $line-height-h2);
    font-weight: $font-weight-bold;
}

h3,
.h3 {
    font-family: $font-lato;
    font-size: $font-size-h3;
    @include line-height( $line-height-h3);
    font-weight: $font-weight-regular;
}

h4,
.h4 {
    font-family: $font-lato-light;
    font-size: $font-size-h4;
    @include line-height( $line-height-h4);
    font-weight: $font-weight-light;
}

h5,
.h5 {
    font-family: $font-lato-light;
    font-size: $font-size-h5;
    @include line-height( $line-height-h5);
    font-weight: $font-weight-light;
}

h6,
.h6 {
    font-family: $font-lato;
    font-size: $font-size-h6;
    @include line-height( $line-height-h6);
    font-weight: $font-weight-bold;
}

.h7 {
    //font-family: $font-lato;
    font-size: $font-size-h7;
    @include line-height( $line-height-h7);
    font-weight: $font-weight-bold;
}

.lead {
    font-size: $font-size-h5;
    font-family: $font-lato;
    @include line-height( $line-height-lead);
    font-weight: $font-weight-bold;
}

.text-light {
    font-weight: $font-weight-light !important;
    font-family: $font-lato-light !important;

    b,
    strong {
        font-family: $font-lato;
        font-weight: $font-weight-regular;
    }
}

.text-regular {
    font-weight: 400;
    font-family: $font-lato;
}

.text-bold {
    font-weight: 600;
    font-family: $font-lato;
}

.text-white {
    color: $white;
}

.text-dark-grey {
    color: $color-grey-5;
}

.text-underline {
    text-decoration: underline;
}

.text-content {
    font-size: $content-text-size;

    > p {
        font-size: $content-text-size;
    }
}

a {
    text-decoration: none;
    color: inherit;
    font-family: $font-lato;

    p & {
        text-decoration: underline;

        &:hover {
            text-decoration: none;
        }
    }

    &[target=_blank] {
        display: inline-block;
        white-space: nowrap;
    }

    &[href^="mailto"] {
        text-decoration: underline;

        &:hover {
            text-decoration: none;
        }
    }

    .text-light:not(.text-light--link) & {
        font-family: $font-lato;
    }

    .text-light--link & {
        font-family: $font-lato-light;
    }
}

.text-right {
    text-align: right;
}

.text-center {
    text-align: center;
}
