.toggle {
    position: relative;
    display: inline-block;
    cursor: pointer;
    text-align: left;
    border: 1px solid $color-grey-3;
    border-radius: 30px;

    .toggle__input:disabled ~ & {
        cursor: default;
    }
}

.toggle__input {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
}

.toggle__text {
    font-size: $font-size-article;
    line-height: 48px;
    padding-left: 30px;
    padding-right: 30px;
    display: block;
    font-weight: 400;
    font-family: $font-lato;
    color: $color-grey-5;
    border-radius: 30px;

    .toggle__input:checked + & {
        background-color: $color-grey-3;
    }
}
