.input {
    font-size: $font-size-h7;
    font-family: $font-lato;
    color: $color-grey-5;
    display: block;
    position: relative;

    input {
        display: block;
        border-radius: 4px;
        border: 1px solid $color-grey-3;
        padding: 9px 15px 10px 14px;
        margin-top: 10px;
        width: 100%;

        &:focus,
        &.is-focus {
            outline: none;
            //border-color: $color-focus;
            box-shadow: 0 0 2px 2px rgba(199, 199, 199, .5);
        }

        &:invalid,
        &.is-invalid {
            border-color: $color-error;
        }

        &::placeholder {
            color: $color-grey-2;
        }
    }

    &--no-label input {
        margin-top: 0;
    }

    &--icon {
        > input {
            padding-right: 2.5em;
        }
    }
}

.input__icon {
    position: absolute;
    right: .5em;
    bottom: .45em;
    font-size: 1.3em;
    //color: $color-base-1;
}
