$section-padding: 60px;
$section-padding-mobile: 40px;

@mixin titleLine() {
    content: "";
    border-bottom: 2px solid;
    position: absolute;
    width: 100vw;
    height: 1px;
    top: 50%;
}

.section {
    padding-top: $section-padding-mobile;
    padding-bottom: $section-padding-mobile;
    position: relative;

    @media (min-width: $break-md) {
        padding-top: $section-padding;
        padding-bottom: $section-padding;
    }

    &--compact {
        padding-top: 20px;
        padding-bottom: 60px;
    }

    &--compact-top {
        padding-top: 0;
    }

    &--compact-bottom {
        padding-bottom: 0;
    }

    &--compact-bottom-small {
        padding-bottom: 40px;
    }

    &--compact-horizontal {
        padding-left: 40px;
        padding-right: 40px;
    }

    &--bg-pattern {
        &:before {
            position: absolute;
            background-repeat: no-repeat;
            background-size: 677px auto;
            background-image: url(../svg/background-pattern-dark.svg);
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            opacity: .5;
            z-index: -1;

            @media (min-width: $break-md) {
                content: "";
            }
        }

        &.section--bg-dark {
            background-color: transparent;

            &:before {
                background-image: url(../svg/background-pattern-light.svg);
                background-color: $color-grey-4;
            }
        }

        &.section--bg-light:before {
            background-image: url(../svg/background-pattern-light.svg);
        }
    }

    &--pattern-bottom:before {
        transform: rotate(180deg);
    }

    &--bg-dark {
        @media (min-width: $break-md) {
            background-color: $color-grey-4;
        }
    }
}

.section__title {
    text-align: center;
    overflow: hidden;
    font-weight: $font-weight-bold;
    margin-bottom: 50px;
    display: flex;
    justify-content: center;
    border-color: $color-base-1;

    &--full {
        margin-left: $container-padding * -1;
        margin-right: $container-padding * -1;

        @media (min-width: $container-with) {
            margin-left: 0;
            margin-right: 0;
        }
    }

    &--normal-header {
        @media (min-width: $break-header) {
            margin-left: 0;
            margin-right: 0;
        }
    }

    span {
        position: relative;
        padding-left: 20px;
        padding-right: 20px;
        text-align: center;
        flex: 0 1 10%;

        @media (min-width: $break-header) {
            flex-basis: auto;
        }

        &:before {
            @include titleLine();
            right: 100%;
        }

        &:after {
            @include titleLine();
            left: 100%;
        }
    }

    &--secondary {
        color: $color-base-4;
        border-color: $color-base-4;

        span {
            @include font-light();
            font-weight: $font-weight-regular;

            &:before,
            &:after {
                border-width: 1px;
            }
        }
    }

    &--compact {
        margin-bottom: 40px;
    }

    .section__title-wraper & {
        overflow: unset;
    }
}

.section__title-wraper {
    position: relative;

    @media (min-width: $break-header) {
        overflow: hidden;
    }

    @media (min-width: $break-lg) {
        margin-left: 40px;
        margin-right: 40px;
    }

}

.section__button {
    text-align: center;
    padding-top: 20px;

    .section--bg-dark.section--bg-pattern & .button {
        background-color: #f7f7f7;

        &:hover {
            background-color: $color-base-5;
        }
    }

    @media (min-width: $break-md) {
        padding-top: 40px;
        padding-bottom: 20px;
    }
}
