$tab-border: 4px;

.tab__nav {
    @include list-unstyled();
    display: block;
    width: 100%;
    border-bottom: $tab-border solid $color-grey-4;
}

.tab__nav-item {
    display: inline-block;
    margin-right: 4px;
}

.tab__nav-link {
    @include font-light();
    display: block;
    border: $tab-border solid transparent;
    background-color: $color-grey-4;
    padding: 25px 35px;
    font-size: ptr(20px);
    color: $color-base-1;
    margin-bottom: - $tab-border;
    z-index: 1;
    position: relative;

    .is-active & {
        @include font-regular();
        font-weight: 600;
        background-color: $white;
        border-color: $color-grey-4;
        border-bottom: none;
        padding-bottom: calc(25px + #{$tab-border});
    }
}

.tab__content {
    border: $tab-border solid $color-grey-4;
    border-top: none;
}

.tab__pane {
    display: none;
    padding: 40px 30px;

    &.is-active {
        display: block;
    }
}
