$check-background: $white;
$check-border: 1px solid $color-grey-3;
$checked-color: $color-base-2;
$check-indicator-color: $color-base-2;
$check-icon-position: 4px;

.check {
    position: relative;
    display: flex;
    align-items: center;
    padding-left: 25px;
    min-height: 1.5em;
    cursor: pointer;
    text-align: left;

    .check__input:disabled ~ & {
        cursor: default;
    }
}

.check__input {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
}

.check__text {
    font-size: $font-size-h7;
    font-weight: 400;
    font-family: $font-lato;
    padding-left: 10px;
    color: $color-grey-5;

    //.check__input:disabled ~ .check__label & {
    //    color: $grey-d;
    //}

    .check--text-normal & {
        font-size: inherit;
        font-weight: inherit;
    }
}

.check__indicator {
    display: block;
    width: 25px;
    height: 25px;
    border-radius: 4px;
    background: $check-background;
    border: $check-border;
    position: absolute;
    top: 0;
    left: 0;
    color: $check-indicator-color;
    padding: 4px;

    //.check__input:not(.is-focus-hidden):focus ~ .check__label & {
    //    @include keyboardFocus();
    //}

    .check__input:disabled ~ .check__label & {
        opacity: .5;
    }

    .check__input:checked + & {
        border-color: $checked-color;
    }
}

.check__icon {
    font-size: ptr(14px);
    position: absolute;
    opacity: 0;
    pointer-events: none;
    color: $checked-color;

    .check__input:checked + .check__indicator & {
        opacity: 1;
    }
}
