.pagination {
    @include list-unstyled();
}

.pagination__item {
    float: left;

    &:not(:first-child) {
        margin-left: 10px;
    }

    &--dots {
        padding-top: .2em;
    }
}

.pagination__button {
    background-color: $color-grey-4;
    //color: $color-base-1;
    border: none;
    width: 30px;
    height: 30px;
    line-height: 30px;
    display: block;

    &:hover,
    .is-selected & {
        background-color: $color-base-1;
        color: $white;
    }
}
