.header {
    color: $white;
    position: relative;
    background-color: $color-base-2;
    z-index: 2;
    padding-top: 70px;

    @media (min-width: $break-header) {
        background-color: transparent;
        padding-top: 0;
    }

    &:before {
        display: none;
        content: "";
        position: absolute;
        background-image: url(../svg/background-pattern-header.svg);
        background-repeat: no-repeat;
        background-size: 677px auto;
        background-position-x: right;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        opacity: .04;

        @media (min-width: $break-header) {
            display: block;
        }
    }
}

.header__vision-impairment {
    $arrow-color: $color-grey-5;
    $arrow-height: 14px;

    background-color: $color-grey-5;
    padding-top: 40px;
    padding-bottom: 40px;
    z-index: 2;
    position: fixed;
    top: 0;
    bottom: 0;
    overflow-y: auto;

    @media (min-width: $break-header) {
        z-index: auto;
        position: static;
        top: unset;
        bottom: unset;
        overflow-y: auto;
    }

    .container:after {
        content: "";
        border-top: $arrow-height solid $arrow-color;
        border-left: $arrow-height/1.5 solid transparent;
        border-right: $arrow-height/1.5 solid transparent;
        position: absolute;
        margin-left: 50px;
        bottom: -$arrow-height;
    }

    .collapse__close {
        &:focus {
            outline: none;
        }

        .button__icon--right {
            color: $white;
        }
    }
}

.header__top {
    background-color: $color-base-1;
    padding-top: 13px;
    padding-bottom: 13px;
    display: none;

    .header--big & {
        size: 30px;
    }

    @media (min-width: $break-header) {
        display: block;
    }
}

.header__bottom {
    background-color: $color-base-2;
    padding-top: 5px;
    padding-bottom: 5px;

    @media (min-width: $break-header) {
        margin-top: 0;
        padding-top: 9px;
        padding-bottom: 0;
    }
}

.test {
    text-align: center;
    @media (min-width: $break-header) {
        text-align: left;
    }
}

.header__logo {
    text-align: center;

    @media (min-width: $break-header) {
        text-align: left;
    }

    .logo {
        font-size: ptr(30px);

        @media (min-width: $break-header) {
            margin-left: 0;
        }
    }
}

.header__container {
    position: relative;

    @media (min-width: $break-lg) {
        padding-left: 40px !important;
        padding-right: 40px !important;
        max-width: calc(#{$break-lg} + (2 * 40px)) !important;
    }
}

.header__link {
    @include font-light();
    color: $white;
    font-size: $font-size-h7;
}

.header__search {
    .autocomplete__input {
        padding-top: 6px;
        padding-bottom: 6px;
        border-color: transparent;
        background-color: rgba($white, .1);
    }

    input {
        background-color: transparent;
        color: $white;
        @include font-light();
    }
}

.header__search-button {
    background: transparent;
    border: none;
    padding: 0;
    line-height: 1;

    .icon {
        margin: 0;
    }
}

.header__button {
    border-color: $white;
    color: $white;
    font-size: $font-size-h7;
    padding-top: 9px;
    padding-bottom: 9px;
    line-height: 1;

    &:not(.button--link) {
        padding-left: 25px;
        padding-right: 25px;

        &:hover {
            background-color: $white;
            color: $color-base-1;
        }
    }
}

.header__login {
    display: none;

    @media (min-width: $break-header) {
        display: block;
    }
}
