$news-border: 1px solid $color-grey-3;
$padding-h: 20px;

.news-list {
    @include list-unstyled;
    border-top: $news-border;
    margin-left: 0;

    .news-list {
        margin-left: -$padding-h * 2;
        margin-right: -$padding-h * 2;
        margin-top: 10px;

        @media (min-width: $break-header) {
            margin-left: -$padding-h;
            margin-right: -$padding-h;
        }

        .news-list__item:last-child {
            border-bottom: none;
        }
    }
}

.news-list__item {
    padding: $padding-h 40px;
    border-bottom: $news-border;

    @media (min-width: $break-header) {
        padding: 10px 20px;
    }
}

.news-list__title {
    font-size: 1.222rem;

    &:hover {
        text-decoration: underline;
    }
}

.new-list__date {
    @include font-light;
}
