$padding-h: 30px;

.project {
    box-shadow: 0 0 2px 1px $color-grey-3;
}

.project__header {
    padding: 18px $padding-h;
    background-color: rgba($color-grey-6, .5);
}

.project___title {
    display: inline-block;
}

.project__actions {
    display: inline-block;
    float: right;
}

.project__content {
    padding: 18px $padding-h;

    &--large {
        padding: 40px $padding-h;
    }

    &--small {
        padding: 20px 10px 10px 10px;
    }
}

.project__label {
    @include font-regular();
    font-size: $font-size-h7;
    margin-bottom: .3em;
}
