.news {
    padding-top: 20px;
    padding-bottom: 60px;

    .news__title {
        font-size: $font-size-h3;

        @media (min-width: $break-header) {
            font-size: $font-size-h1;
        }
    }

    p {
        font-size: $font-size-article;
    }

    h2 {
        font-size: $content-text-size;
    }

    h2 + p {
        margin-top: 0;
    }

    figure {
        position: relative;
        left: -40px;
        width: 100vw;
        max-width: 780px;
        margin-top: 50px;
        margin-bottom: 40px;

        @media (min-width: $break-header) {
            width: 100%;
            left: unset;
            max-width: unset;
        }

        img:not(.full-image__image) {
            display: block;
            max-width: 100%;
            height: auto;
            margin: 0 auto;
        }
    }

    .full-image {
        position: static;
    }
}
