$icon-width: 22px;

ul.list {
    list-style-type: none;
    color: inherit;
    padding-left: 0;
    font-size: ptr(22px);

    > li {
        @include font-light();
        padding-left: calc(#{$icon-width} + 16px);
        position: relative;

        &:before {
            content: "";
            width: 6px;
            height: 6px;
            border-radius: 50%;
            display: inline-block;
            background-color: $color-base-1;
            position: absolute;
            left: 0;
            top: .72em;
            @include vision-height("top", .5em);
            transform: translateY(-50%);
        }
    }
}

ol.list {
    color: inherit;
    list-style-type: none;
    counter-reset: c1;
    padding-left: 0;
    margin-left: 10px;

    > li {
        @include font-light();

        &:before {
            counter-increment: c1;
            content: counters(c1, ".") ". ";
            font-weight: 600;
            font-family: $font-lato;
            font-size: .9em;
            padding-right: 5px;
        }

        > ol {
            padding-left: 25px;
        }
    }
}

.list--icon {
    list-style-type: none !important;
    padding-left: 0;
    margin-left: 0;

    li {
        padding-left: calc(#{$icon-width} + 16px);
        position: relative;

        &:before {
            display: none !important;
        }
    }
}

.list__icon {
    color: $color-grey-3;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    font-size: $icon-width;
}
