body {
    display: block;
    overflow-x: hidden;

    &.scroll-disabled {
        overflow-y: scroll;
        position: fixed;
        width: 100%;
    }

    &.scroll-disabled--hide-scrollbar {
        overflow-y: hidden;
    }
}

.container {
    width: 100%;
    max-width: $container-with;
    margin: 0 auto;
    padding-left: $container-padding;
    padding-right: $container-padding;

    @media (min-width: $container-with) {
        padding-left: 0;
        padding-right: 0;
    }

    &--full-width-mobile {
        padding-left: 0;
        padding-right: 0;
    }

    &--compact-horizontal {
        @media (min-width: $break-header) {
            padding-left: 40px;
            padding-right: 40px;
        }
    }

    &--content {
        max-width: 780px;

        .container & {
            padding: 0;

            p {
                font-size: $font-size-article;
            }
        }
    }

    &--relative {
        position: relative;
    }
}

.is-freezed {
    overflow: hidden !important;
}

.pointer {
    cursor: pointer !important;
}

.list-unstyled {
    @include list-unstyled();
}

.clerfixer {
    @include clearFixer();
}

.inline-block {
    display: inline-block;
}

@mixin responsive-utils($name) {
    .hidden-to-#{$name} {
        display: block;
    }
}

[class^="hidden-to-"] {
    display: none;
}

@media only screen and (min-width: $break-sm) {
    @include responsive-utils(sm);
}

@media only screen and (min-width: $break-md) {
    @include responsive-utils(md);
}

@media only screen and (min-width: $break-header) {
    @include responsive-utils(header);
}

@media only screen and (min-width: $break-lg) {
    @include responsive-utils(lg);
}
