.button.filter {
    border: 1px solid $color-grey-3;
    color: $color-grey-5;

    &:focus {
        outline: none;
    }

    &.is-active {
        background-color: $color-grey-3;
    }

    &:hover {
        background-color: $color-grey-4;
        color: $color-grey-5;
        border-color: $color-grey-4;
    }

}

.filter--all .is-active:not(.filter__all) {
    background-color: $white;
}
