.service {
    @include clearFixer();
    text-align: center;
    display: block;
    padding-bottom: 30px;
    margin-bottom: -60px;

    @media (min-width: $break-md) {
        padding-bottom: 0;
        margin-bottom: 0;
    }

    &:not(&--first) {
        border-top: 2px solid $color-grey-4;

        @media (min-width: $break-md) {
            border-top: none;
        }
    }
}

.service__icon {
    font-size: ptr(50px);
    color: $color-base-4;
    margin-top: 26px;
    margin-bottom: 13px;

    @media (min-width: $break-md) {
        margin-top: 0;
    }
}

.service__title {
    font-weight: $font-weight-bold;

    .service:hover & {
        text-decoration: underline;
    }
}

.service__content {
    padding-top: .5em;
    @include font-light();
    font-size: $font-size-h7;
    color: $color-grey-5;
}

.service__link {
    @include font-light();
    color: $color-grey-5;
    text-decoration: underline;
    font-size: $font-size-h7;
}
