.whitespace {
    > * + * {
        margin-top: 1em;
    }

    > :last-child {
        margin-bottom: 0;
    }

    > :first-child {
        margin-top: 0;
    }

    &--horizontal {
        > * + * {
            margin-left: 10px;
            margin-top: 0;
        }
    }

    &--xs {
        > * + * {
            margin-top: .5rem;
        }
    }

    &--double {
        > * + * {
            margin-top: 20px;
        }
    }

    &--small {
        > * + * {
            margin-top: 30px;
        }
    }

}

.whitespace__none {
    &,
    .whitespace & {
        margin-top: 0;
    }
}

.whitespace__normal {
    &,
    .whitespace & {
        margin-top: 1em;
    }
}

.whitespace__double {
    &,
    .whitespace & {
        margin-top: 20px;
    }
}

.whitespace__small {
    &,
    .whitespace & {
        margin-top: 30px;
    }
}

.whitespace__medium {
    &,
    .whitespace & {
        margin-top: 40px;
    }
}

.whitespace__large {
    &,
    .whitespace & {
        margin-top: 60px;
    }
}
