.form-group {
    display: block;

    &--inline {
        padding-top: 10px;

        > label,
        > * {
            display: inline-block;
        }

        > * + *,
        > label + label,
        > .radio + .radio {
            margin-left: 15px;
            margin-top: 0;
        }
    }
}
