.progress {
    @include list-unstyled();
    list-style-type: none;
    display: flex;
    counter-reset: progress-counter;
}

.progress__item {
    flex: 1 1 0;
    text-align: center;
    counter-increment: progress-counter;
    position: relative;

    &:not(:first-child):after {
        content: "";
        border-bottom: 4px dashed $color-grey-6;
        width: 100%;
        right: 50%;
        left: auto;
        display: block;
        top: calc(72px / 2);
        position: absolute;
        transform: translateY(-50%);
    }

    &.is-done,
    &.is-current {
        &:after {
            border-bottom-style: solid;
        }
    }
}

.progress__link {
    display: inline-block;
}

.progress__counter {
    border: 4px solid $color-grey-6;
    border-radius: 50%;
    width: 72px;
    height: 72px;
    display: flex;
    align-items: center;
    text-align: center;
    margin: auto;
    background-color: $white;
    z-index: 1;
    position: relative;

    &:before {
        @include font-light();
        content: counter(progress-counter);
        font-size: ptr(50px);
        margin: 0 auto;
    }

    .is-current & {
        background-color: $color-grey-6;

        &:before {
            @include font-regular();
            font-weight: 600;
        }
    }

    .is-done & {
        background-color: $color-grey-6;

        &:before {
            display: none;
        }
    }
}

.progress__icon {
    display: none;
    font-size: ptr(40px);

    .is-done & {
        display: inline-block;
        margin: 0 auto;
    }
}

.progress__title {
    @include font-light();
    font-size: $font-size-h5 ;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 10px;
    color: $color-grey-5;

    .is-current & {
        @include font-regular();
        font-weight: 700;
    }
}
