$label-offset: 7px;
$corner-height: 7px;

.label {
    background-color: $color-extra-2;
    position: absolute;
    top: 10px;
    left: -$label-offset;
    color: $white;
    padding: 9px 15px;
    font-size: $font-size-small;
    z-index: 1;
    @include font-light();

    &:before {
        content: "";
        height: 1px;
        width: 1px;
        border-top: $corner-height solid transparent;
        border-right: $label-offset solid $color-grey-1;
        border-bottom: none;
        left: 0;
        top: - $corner-height;
        position: absolute;
    }

    & strong,
    & b {
        font-family: $font-lato;
        font-weight: 400;
    }

    &--top {
        margin-left: 40px;
        margin-right: 40px;
        top: -$corner-height; //10px;
        left: 0;

        &:before {
            top: 0;
            left: - $label-offset;
        }
    }

    p {
        font-size: inherit !important;
    }
}
