$icon-margin: 10px;

@mixin button-variant($background, $color, $border, $h-background, $h-border: $h-background, $h-color: $h-border ) {
    background-color: $background;
    border-color: $border;
    color: $color;

    &:hover {
        background-color: $h-background;
        border-color: $h-border;
        color: $h-color;
    }
}

.button {
    font-family: $font-lato;
    font-size: $button-font-size;
    padding: $button-padding-mobile;
    line-height: $button-lineheight;
    border: $button-border;
    border-radius: 2em;
    background: none;
    text-align: center;
    text-decoration: none;
    display: inline-block;

    @media (min-width: $break-header) {
        padding: $button-padding;
    }

    &--primary {
        @include button-variant($color-base-5, $color-base-2, $color-base-5, transparent, $color-base-4);

        &.button--light {
            @include button-variant($color-base-5, $color-base-2, $color-base-5, $white, $color-base-4);
        }

        &.is-disabled,
        &[disabled=true] {
            @include button-variant($color-grey-4, $color-grey-2, $color-grey-4, $color-grey-4, $color-grey-4, $color-grey-2);
            pointer-events: none;
        }
    }

    &--secondary {
        @include button-variant(transparent, $color-base-4, $color-base-4, $color-base-5, $color-base-5, $color-base-2);

        &.is-disabled,
        &[disabled=true] {
            @include button-variant(transparent, $color-grey-2, $color-grey-4, $color-grey-4, $color-grey-4, $color-grey-2);
            pointer-events: none;
        }
    }

    &--light {
        background-color: $white;
    }

    &--link {
        padding-left: 0;
        padding-right: 0;
        line-height: 50px;
        font-size: $font-size-base;
        border: none;

        &:hover {
            text-decoration: underline;
        }

        &.is-disabled,
        &[disabled=true] {
            color: $color-grey-2;
            pointer-events: none;
        }
    }

    &--small {
        font-size: $font-size-base;
    }

    &--full {
        width: 100%;
    }

    &--full-block-sm {
        width: 100%;
        max-width: 295px;
        @media (min-width: $break-md) {
            width: auto;
            max-width: none;
        }
    }

    &--icon {
        border: 1px solid transparent;
        color: $color-grey-5;
        border-radius: 1em;
        padding: .5em 2.35em .6em .5em;
        font-size: $font-size-h7;
        text-align: center;
        position: relative;

        &:before {
            content: "";
            color: $color-grey-5;
            border: 1px solid $color-grey-5;
            border-radius: 1em;
            padding: .17em;
            font-size: calc(2.1em + 1px);
            position: absolute;
            top: -1px;
            right: -1px;
            height: 35px;
            width: 35px;
        }

        &:hover {
            border: 1px solid $color-grey-5;

            &:before {
                display: none;
            }
        }

        &:focus {
            outline: none;
        }

        &.is-disabled,
        &[disabled=true] {
            color: $color-grey-2;
            pointer-events: none;

            &:before {
                border-color: $color-grey-2;
            }
        }
    }

    &--mini {
        $btn-size: 35px;
        border: 1px solid $color-grey-5;
        color: $color-grey-5;
        border-radius: 2em;
        padding: 0;
        font-size: $font-size-h7;
        text-align: center;
        width: $btn-size;
        height: $btn-size;
        max-width: $btn-size;
        max-height: $btn-size;
        line-height: calc( #{$btn-size} - 2px);
        //display: flex;

        &:hover {
            background-color: $color-grey-5;
            border-color: $color-grey-5;
            color: $white;
        }

        a & {
            vertical-align: top;
            line-height: $btn-size;
        }

        &.is-disabled,
        &[disabled=true] {
            color: $color-grey-2;
            border-color: $color-grey-2;
            pointer-events: none;
        }
    }
}

.button__icon {
    &--left {
        margin-right: $icon-margin;
        font-size: .888888rem;
    }

    &--right {
        margin-left: $icon-margin;
        font-size: .888888rem;
    }

    .button--icon & {
        color: $color-grey-5;
        padding: .25em;
        font-size: calc(2.1em + 1px);
        position: absolute;
        top: -1px;
        right: -1px;
    }

    .button--icon-only & {
        overflow: visible;
        padding: .25em;
    }

    .button--icon:hover & {
        border-color: transparent;
    }

    .button.is-disabled &,
    .button[disabled=true] & {
        color: $color-grey-2;
        border-color: $color-grey-2;
        pointer-events: none;
    }
}

.btn-group {
    > .button {
        margin-left: 10px;
    }
}
