.main {
    overflow: hidden;
    padding-bottom: 40px;

    @media (min-width: $break-md) {
        padding-bottom: 0;
    }

    @media (min-width: $break-header) {
        padding-top: 50px;
    }

    &--contentPage {
        padding-top: 20px;
        padding-bottom: 80px;

        @media (min-width: $break-header) {
            padding-top: 50px;
        }
    }

    &--contentPage-top {
        padding-top: 20px;
        padding-bottom: 0;

        @media (min-width: $break-header) {
            padding-top: 50px;
        }
    }

    &--project {
        padding-top: 140px;
        padding-bottom: 40px;
        overflow: unset;
    }
}
