.sitemap {
    column-count: 3;
    column-width: 300px;
    height: 100%;
    column-fill: auto;
    column-gap: 20px;
    margin-bottom: -20px;

    a {
        color: $color-base-1;

        &:hover {
            text-decoration: underline;
        }
    }
}

.sitemap__title {
    font-size: $font-size-article;
    color: $color-grey-5;
}

.sitemap__item {
    display: inline-block;
    width: 100%;
    margin-bottom: 40px;
}

.sitemap__list {
    @include list-unstyled();
    margin-left: 20px;
    padding-top: .5em;

    li {
        line-height: 1.67;
    }
}
