@import "daterangepicker";

.daterangepicker {
    border: 1px solid $color-grey-3;
    border-radius: 4px;
    display: none;
    width: auto;

    &::after,
    &::before {
        display: none;
    }

    &.show-calendar {
        display: inline-block;
        margin-top: 5px;
    }

    th,
    td {
        font-family: $font-lato;
        color: $color-grey-5;
        font-size: $font-size-h7;
        height: 30px;
        width: 30px;
        min-width: 0;
    }

    .month {
        font-size: $font-size-base;
    }

    th.available:not(.active):hover,
    td.available:not(.active):hover {
        background-color: transparent;

        &:before {
            content: "";
            display: block;
            border-radius: 50% !important;
            border: 1px solid $color-grey-3;
            position: absolute;
            width: 30px;
            height: 30px;
            margin-top: -4px;
        }
    }

    td.active {
        background-color: $color-base-2;
        border-radius: 50% !important;
    }

    //&.off {
    //    visibility: hidden;
    //}

    .fa {
        width: 20px;
        height: 20px;
        display: inline-block;

        &:before {
            content: "";
            border-style: solid;
            border-color: $color-base-3;
            width: 10px;
            height: 10px;
            display: inline-block;
            transform: rotate(45deg);
        }

        &.fa-chevron-right {
            padding-right: 5px;

            &:before {
                border-width: 1px 1px 0 0;
            }
        }

        &.fa-chevron-left {
            padding-left: 5px;

            &:before {
                border-width: 0 0 1px 1px;
            }
        }
    }
}
