$border: 1px solid $color-grey-4;

.card {
    color: $color-text-base;
    display: block;
    background-color: $white;
}

.card__content {
    @include font-light();

    padding: 30px;
    text-align: center;
    border-bottom: $border;
    margin-bottom: 20px;

    @media (min-width: $break-md) {
        border-bottom: none;
        margin-bottom: 0;
    }

    &--sections {
        padding: 0 0 20px 0;
        text-align: left;

        & > p {
            padding: 20px 40px;

            &:not(:first-of-type) {
                border-top: $border;
            }
        }
    }

    &--small {
        padding: 20px 20px 30px 20px;
    }

    .card--light & {
        border-left: $border;
        border-right: $border;
        border-bottom: $border;
    }

    .card--align-left & {
        text-align: left;
    }

    .card--text-small & {
        font-size: $font-size-small;
    }
}

.card__image {
    position: relative;
    background-color: $white;

    &:before {
        @media (min-width: $break-header) {
            content: "";
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
        }

        a.card & {
            background-color: rgba($color-grey-4, .4);
        }
    }

    .card:hover &:before {
        opacity: 0;
    }

    > img {
        display: block;
        max-width: 100%;
        height: auto;
        margin: 0 auto;
    }
}

.card__title {
    font-size: $font-size-h6;
    line-height: $line-height-h6;

    a.card:hover & {
        text-decoration: underline;
    }

    .card--text-small & {
        font-size: $font-size-small;
        line-height: normal;
    }

    &--main {
        text-align: center;
        min-height: 100px;
        background-color: $color-base-1;
        color: $white;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: $font-size-h2;
        line-height: $line-height-h2;
        padding-left: 20px;
        padding-right: 20px;

        .card--wisteria & {
            background-color: $color-extra-1;
        }

        .card--jaffa & {
            background-color: $color-extra-2;
        }

        .card--carissma & {
            background-color: $color-extra-3;
        }
    }

}
