[data-toggle="collapse"] {
  cursor: pointer;
}

.collapse {
  display: none;
  position: relative;
}

.collapse--visible, .collapse.is-visible {
  display: block;
}

.collapse--collapsing, .collapse.is-collapsing {
  position: relative;
  height: 0;
  overflow: hidden;
  transition: height .3s ease-in-out;
}

.collapse__container {
  background: #ffffff;
  padding: 40px 20px 20px;
  margin-top: 10px;
}

@media (min-width: 375px) {
  .collapse__container {
    padding: 40px;
  }
}

.collapse__container--no-shadow {
  box-shadow: none;
}

