.collapse__close {
  display: none;
  position: absolute;
  z-index: 1;
  color: #4a4a4a;
  font-size: 0.88889rem;
  top: 1em;
  right: 1em;
  border: none;
}

.collapse--visible .collapse__close,
.collapse.is-visible .collapse__close {
  display: block;
}
