
.vision-impairment {
    background-color: $color-grey-5;
    color: $white;
    line-height: $line-height-base;
    font-size: $font-size-base;
}

.vision-impairment__title {
    font-size: pte(28px);
}

.vision-impairment__label {
    font-size: pte(16px);

    &--md {
        font-size: pte(20px);
    }

    &--lg {
        font-size: pte(22px);
    }
}

.vision-impairment__button {
    font-size: pte(20px);
}

.vision-impairment__actions {
    margin-top: 20px;
}

.vision__font {
    &--large {
        font-size: $font-size-base + $font-coefficient;
    }

    &--extraLarge {
        font-size: $font-size-base + 2 * $font-coefficient;
    }
}

.vision__line {
    &--large {
        line-height: $line-coeffitcent;
    }

    &--extraLarge {
        line-height: $line-coeffitcent * 1.5;
    }
}

.vision__contrast {

    background-color: #000000;

    * {
        &:not(.full-image, .gallery__slide) {
            background-image: none !important;
        }
        background-color: $contrast-dark !important;
        border-color: $contrast-light !important;
        color: $contrast-light !important;

        &:before,
        &:after {
            background-image: none !important;
        }

        &::placeholder {
            color: $contrast-light !important;
        }

    }

    a:not(.main-menu__link):not(.button) {
        text-decoration: underline;

        &:hover {
            text-decoration: none;
        }
    }

    .map {
        background-color: rgb(229, 227, 223) !important;

        .button {
            background-color: $contrast-light !important;

            &:hover {
                border: 1px solid $contrast-dark !important;
            }
        }

        & * {
            background-color: unset !important;
            color: $contrast-dark !important;
        }
    }

    .filter.is-active {
        background-color: $contrast-light !important;
        color: $contrast-dark !important;

        &:hover {
            background-color: $contrast-dark !important;
            color: $contrast-light !important;
        }
    }

    .filter--all .is-active:not(.filter__all),
    .filter {
        background-color: $contrast-dark !important;
        color: $contrast-light !important;

        &:hover {
            background-color: $contrast-light !important;
            color: $contrast-dark !important;
        }
    }

    .table-button,
    .table-button * {
        background-color: $contrast-light !important;
        color: $contrast-dark !important;
    }

    .exhibition,
    .exhibition * {
        background-color: $contrast-light !important;
        color: $contrast-dark !important;
    }

    .full-image .full-image__container {
        background-color: unset !important;
    }

    .full-image__image {
        background-image: unset !important;
    }

    [class*="card__title--"] {
        background-color: $contrast-light !important;
        color: $contrast-dark !important;
    }

    .banner__background {
        visibility: hidden;
    }

    .footer {
        border-top: 2px solid;
    }

    .header__vision-impairment {

        border-bottom: 2px solid;

        .container:after {
            border-top-color: $contrast-light;
        }
    }

    ul.list > li:before {
        background-color: $contrast-light !important;
    }

    .mobile-menu:before {
        background-color: $contrast-dark !important;
    }

    .mobile-menu__header,
    .submenu__header {
        border-bottom: 2px solid;

        @media (min-width: $break-header) {
            border-bottom: none;
        }
    }

    .section__title {
        border-color: $contrast-light !important;
    }

    th {
        background-color: $contrast-light !important;
        color: $contrast-dark !important;
    }
}
