body {
  display: block;
  overflow-x: hidden;
}

body.scroll-disabled {
  overflow-y: scroll;
  position: fixed;
  width: 100%;
}

body.scroll-disabled--hide-scrollbar {
  overflow-y: hidden;
}

.container {
  width: 100%;
  max-width: 1180px;
  margin: 0 auto;
  padding-left: 40px;
  padding-right: 40px;
}

@media (min-width: 1180px) {
  .container {
    padding-left: 0;
    padding-right: 0;
  }
}

.container--full-width-mobile {
  padding-left: 0;
  padding-right: 0;
}

@media (min-width: 900px) {
  .container--compact-horizontal {
    padding-left: 40px;
    padding-right: 40px;
  }
}

.container--content {
  max-width: 780px;
}

.container .container--content {
  padding: 0;
}

.container .container--content p {
  font-size: 1.222rem;
}

.container--relative {
  position: relative;
}

.is-freezed {
  overflow: hidden !important;
}

.pointer {
  cursor: pointer !important;
}

.list-unstyled {
  list-style-type: none;
  padding-left: 0;
}

.clerfixer:after {
  content: "";
  display: table;
  clear: both;
}

.inline-block {
  display: inline-block;
}

[class^="hidden-to-"] {
  display: none;
}

@media only screen and (min-width: 375px) {
  .hidden-to-sm {
    display: block;
  }
}

@media only screen and (min-width: 600px) {
  .hidden-to-md {
    display: block;
  }
}

@media only screen and (min-width: 900px) {
  .hidden-to-header {
    display: block;
  }
}

@media only screen and (min-width: 1180px) {
  .hidden-to-lg {
    display: block;
  }
}
