//$table-padding: 14px 20px 16px 20px;
$table-padding: 8px 10px 12px 10px;
$side-padding: 30px;

.table {
    border-collapse: collapse;
    table-layout: auto;
    width: $break-lg;
    font-family: $font-lato;

    @media (min-width: $break-lg) {
        width: 100%;
    }

    td,
    th {
        font-size: $font-size-h7;
        padding: $table-padding;

        &:first-child {
            padding-left: $side-padding;
        }

        &:last-child {
            padding-right: $side-padding;
        }
    }

    th {
        font-weight: $font-weight-bold;
        background-color: rgba($color-grey-6, .5);
        text-align: left;

        &.nowrap {
            white-space: nowrap;
        }
    }

    td {
        background-color: $white;
        vertical-align: top;

        &.vertical-middle {
            vertical-align: middle;
        }
    }

    &--striped,
    &[data-table="table--striped"] {
        tr:nth-child(even) td {
            //background-color: rgba($color-grey-4, .5);
            background-color: $color-grey-4;
        }
    }

    &--bordered,
    &[data-table="table--bordered"] {
        td,
        th {
            border: 1px solid $color-grey-3;
        }
    }

    &--bordered-horizontal,
    &[data-table="table--bordered-horizontal"] {
        td,
        th {
            padding-top: 10px;
            padding-bottom: 14px;
            color: $color-grey-5;
        }

        td {
            border-bottom: 1px solid $color-grey-3;
        }

        th {
            font-weight: 700;
        }

        tr:hover {
            > td:not(.table__footer) {
                background-color: $color-grey-4;
            }

            &:first-child {
                > td {
                    border-top: 1px solid $color-grey-3;
                    padding-top: 9px;
                }
            }
        }
    }

    &--full-width {
        width: 100%;
    }
}

.table__row-error {
    td {
        color: $color-error !important;
        font-weight: 600;
    }
}

.table-wrapper {
    position: relative;
    //margin-bottom: 2em;

    &--scroller {
        overflow-x: auto;
    }

    &--content-width,
    &--fullwith {
        width: calc(100vw);
        left: 50%;
        transform: translateX(-50%);
        max-width: $container-with;
    }

    &--content-width {
        padding-left: $container-padding;
        padding-right: $container-padding;
    }
}

.table-button {
    position: absolute;
    top: 200px;
    width: 50px;
    height: 50px;
    margin-top: -25px;
    border-radius: 50%;
    cursor: pointer;
    text-align: center;
    line-height: 50px;
    background-color: $color-extra-1;
    color: $white;
    display: none;
    font-size: 1.556em;

    &--prev {
        left: 50px;
        padding: 3px 5px 3px 3px;

        .scrollPrev & {
            display: block;
        }
    }

    &--next {
        right: 50px;
        padding: 3px 3px 3px 5px;

        .scrollNext & {
            display: block;
        }
    }

    .nav-top & {
        position: absolute;
        top: 200px;
    }

    /*    .nav-bottom & {
            position: absolute;
            bottom: 100px;
            top: auto;
        }*/
}

.table__footer {
    padding: 20px 30px;
}

.table__link {
    color: $color-base-1;
    font-size: $font-size-base;
}

@for $i from 1 through $grid-columns {
    .table--col-#{$i} {
        width: 100% / $grid-columns * $i;
    }
}
