$notice-color: $color-error;
$icon-width: 90px;

@mixin color-notice($color) {

    border-color: $color;

    .notice__cell--icon {
        background-color: $color;
    }
}

.notice {
    border: 1px solid;
    display: flex;
    align-items: stretch;
    position: relative;
    background-color: $white;
    color: $color-grey-1;

    &--error {
        @include color-notice($color-error);
    }

    &--success {
        @include color-notice($color-success);
    }

    &--info {
        @include color-notice($color-focus);
    }
}

.notice__cell {
    &--icon {
        //background-color: $notice-color;
        color: $white;
        //color: #0000ff;
        padding: 18px 20px 19px 19px;
        min-width: $icon-width;
        display: flex;
        align-items: center;

        &:before {
            //content: "";
            //background-color: $notice-color;
            position: absolute;
            left: 1px;
            top: 0;
            bottom: 0;
            min-width: $icon-width;
            //z-index: -1;
        }
    }

    &--content {
        flex-grow: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 20px 30px;

        @media (min-width: $break-md) {
            flex-direction: row;
        }
    }
}

.notice__content {
    flex-grow: 1;
    font-size: $font-size-h5;
    line-height: normal;
}

.notice__action {
    text-align: right;
    padding-left: 30px;
    align-self: flex-end;
    margin-top: 30px;

    @media (min-width: $break-md) {
        align-self: center;
        margin-top: 0;
    }
}

.notice__button {
    padding: 0;
    line-height: 1;
    white-space: nowrap;
    color: $color-base-2;
}

.notice__icon {
    margin: auto;
    font-size: 2.7778rem;
}
