$accordion-color: $color-grey-4;
$acordion-border: 4px solid $accordion-color;

.accordion {
    background-color: $white;
}

.accordion__item {
    position: relative;

    & + & {
        margin-top: 4px;
    }

    @media (min-width: $break-header) {
        border: $acordion-border;
    }
}

.accordion__collapse {
    .accordion__item:last-child & {
        border-bottom: $acordion-border;

        @media (min-width: $break-header) {
            border-bottom: none;
        }
    }
}

.accordion__title {
    background-color: $accordion-color;
    display: block;
    padding: 18px 20px 19px 70px;
    font-family: $font-lato;
    font-size: $font-size-h2;
    line-height: $line-height-h2;
    font-weight: $font-weight-bold;
    @include clearFixer();

    @media (min-width: $break-header) {
        padding-right: 30px;
    }

    &.collapsed,
    > .collapsed {
        .accordion__icon--down {
            display: inline-block;
        }

        .accordion__icon--up {
            display: none;
        }
    }
}

.accordion__icon {
    &--down {
        position: absolute;
        left: 20px;
        margin-top: .1em;
        vertical-align: middle;
        display: none;
        color: $color-extra-1;
    }

    &--up {
        position: absolute;
        left: 20px;
        margin-top: .1em;
        vertical-align: middle;
        display: inline-block;
        color: $color-extra-1;
    }
}

.accordion__body {
    padding: 40px;

    @media (min-width: $break-header) {
        padding: 30px;
    }
}

