.autocomplete {
    position: relative;
}

.autocomplete__input {
    border: 1px solid $color-grey-3;
    border-radius: 30px;
    padding: 7px 15px;
    display: flex;
    align-items: center;
    background-color: $white;

    input {
        border: none;
        font-size: $font-size-h7;
        min-width: 0;
        flex: 1 auto;

        &:focus {
            outline: none;
        }

        &::placeholder {
            color: inherit;
        }
    }

    .autocomplete--large & {
        input {
            height: 44px;
            font-size: 1.222em;
        }
    }
}

.autocomplete__icon {
    margin: .1em 10px;

    &:first-child {
        margin-left: 0;
    }

    &:last-child {
        margin-right: 0;
    }

    .autocomplete--large & {
        font-size: 1.222em;
    }
}

.autocomplete__list {
    display: none;
    @include list-unstyled();
    border: 1px solid $color-grey-3;
    border-radius: 4px;
    position: absolute;
    margin-top: 7px;
    width: 100%;

    //TODO: make visible
}

.autocomplete__item {
    padding: 5px 10px;
    line-height: normal;

    &:hover {
        background-color: $color-grey-4;
    }
}
