.image {
    width: 100%;
    position: relative;

    &--content {
        margin-top: 50px !important;
        margin-bottom: 40px;
    }
}

.image__image {
    display: block;
    max-width: 100%;
    height: auto;
    margin: 0 auto;
}

$label-offset: 7px;
$corner-height: 7px;

figcaption {
    background-color: $color-extra-2;
    position: absolute;
    color: $white;
    padding: 9px 15px;
    font-size: $font-size-small;
    z-index: 1;
    margin-left: 40px;
    top: -$corner-height; //10px;
    left: 0;
    @include font-light();

    &:before {
        content: "";
        height: 1px;
        width: 1px;
        border-top: $corner-height solid transparent;
        border-right: $label-offset solid $color-grey-1;
        border-bottom: none;
        top: 0;
        left: - $label-offset;
        position: absolute;
    }

    & strong,
    & b {
        font-family: "Lato";
        font-weight: 400;
    }
}
