$menu-border-mobile: 1px solid rgba(#ffffff, .5);
$menu-padding: 22px 17px;

@mixin mobileMenuArrow-left() {
    &:before {
        content: "";
        width: 14px;
        height: 14px;
        border-left: 2px solid $white;
        border-top: 2px solid $white;
        display: inline-block;
        transform: rotate(-45deg);
        margin-right: 5px;
        top: -.05em;
        position: relative;

        @media (min-width: $break-header) {
            display: none;
        }
    }
}

@mixin mobileMenuArrow-right() {
    &:after {
        content: "";
        width: 14px;
        height: 14px;
        border-left: 2px solid $color-base-1;
        border-top: 2px solid $color-base-1;
        display: inline-block;
        transform: rotate(135deg);
        margin-left: 5px;
        top: .3em;
        position: relative;
        float: right;

        @media (min-width: $break-header) {
            display: none;
        }
    }
}

.mobile-menu {
    @include list-unstyled;
    margin-left: 0;

    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 1;
    border-bottom: $menu-border-mobile;

    &.is-open {
        bottom: 0;
        overflow-y: auto;
        //background-color: $color-grey-4;
    }

    &.is-freezed {
        overflow-y: hidden;
    }

    @media (min-width: $break-header) {
        position: inherit;
        border: none;
        background-color: transparent;
    }

    &:before {
        content: "";
        background-color: $color-base-1;
        position: absolute;
        left: 0;
        top: 0;
        height: 70px;
        right: 0;
        z-index: -1;
    }
}

.mobile-menu__item {
    &--right {
        float: right;
        border-left: $menu-border-mobile;

        .mobile-menu__item {
            border-right: $menu-border-mobile;
        }
    }

    &--left {
        float: left;

        @media (min-width: $break-header) {
            float: none;
        }
    }

    &:not(.mobile-menu__item--deskdop) {
        @media (min-width: $break-header) {
            display: none;
        }
    }
}

.mobile-menu__link {
    color: inherit;
    padding: $menu-padding;
    display: inline-block;
    line-height: 1;

    > .icon {
        font-size: 1.388rem;
    }

    @media (min-width: 375px) {
        padding: 22px 23px;
    }

    @media (min-width: $break-header) {
        display: none;
    }
}

.mobile-menu__button {
    background: none;
    border: none;
    font-size: $font-size-h5;
    display: flex;
    align-items: center;

    > .icon {
        font-size: 1.388rem;
        margin-right: 10px;
    }

    @media (min-width: $break-header) {
        display: none;
    }
}

.main-menu {
    @include list-unstyled();
    display: none;
    width: 100%;
    background-color: $color-grey-4;

    @at-root .is-selected + & {
        display: block;
        position: absolute;
        top: 0;
        min-height: 100%;
    }

    @media (min-width: $break-header) {
        display: flex;
        justify-content: space-between;
        background-color: transparent;
        margin-top: 2px;
    }
}

.main-menu__item {
    display: block;

    @media (min-width: $break-header) {
        display: inline;
        flex: 0 auto;

        .ua-ie-9 & {
            margin-right: 20px;
        }
    }

}

.main-menu__link {
    display: block;
    color: $color-base-1;
    font-size: 1.333rem;
    padding: 17px 30px 17px 30px;
    background-color: $color-grey-4;
    border-bottom: 1px solid $color-grey-3;

    @media (min-width: $break-header) {
        @include font-light();
        border-bottom: 2px solid transparent;
        color: $menu-color;
        font-size: $menu-font-size;
        text-decoration: none;
        padding: 0 0 11px 0;
        display: inline-block;
        white-space: nowrap;
        background-color: transparent;
    }

    .is-selected > & {
        @media (min-width: $break-header) {
            font-family: $font-lato;
            font-weight: 600;
            border-bottom-color: $white;
        }
    }

    &:hover {
        @media (min-width: $break-header) {
            border-bottom-color: $white;
        }
    }

    &--has-sub {
        @include mobileMenuArrow-right();
    }
}

.main-menu__close {
    background: none;
    border: none;
}

.submenu {
    //background-color: $color-base-1;
    background-color: $color-grey-4;
    transition: left .3s ease-out;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 100%;
    width: 100%;
    overflow-y: auto;
    z-index: 1;

    .is-selected + & {
        left: 0;

        @media (min-width: $break-header) {
            @include clearFixer();
            left: 50%;
        }
    }

    &--no-slide {
        transition: none;
    }

    @media (min-width: $break-header) {
        display: none;
        position: absolute;
        background-color: $white;
        z-index: 1;
        top: 100%;
        right: 0;
        bottom: initial;
        color: $color-base-1;
        padding-top: 40px;
        padding-bottom: 40px;
        left: 50%;
        width: 100vw;
        transform: translateX(-50vw);
        transition: none;
        box-shadow: 0 1px 5px 0 rgba($color-grey-5, .8);

        &:before {
            transform: rotate(180deg);
            content: "";
            position: absolute;
            background-repeat: no-repeat;
            background-size: 677px auto;
            background-image: url(../svg/background-pattern-dark.svg);
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            opacity: .5;
            z-index: -1;
        }

        .main-menu__item:hover > & {
            display: inherit;
        }
    }

    .container {
        @media (max-width: $break-header - 1) {
            padding: 0;
        }
    }

    .vision__contrast & {
        border-bottom: 2px solid;
    }
}

.mobile-menu__header,
.submenu__header {
    background-color: $color-base-1;

    @media (min-width: $break-header) {
        background-color: transparent;
    }
}

.submenu__title {
    @include font-light();
    font-size: ptr(24px);
    cursor: pointer;
    color: inherit;
    padding: 20px 25px;
    display: inline-block;

    &.submenu__title--arrow {
        @include mobileMenuArrow-left();
    }

    @media (min-width: $break-header) {
        font-weight: 600;
        font-family: $font-lato;
        font-size: ptr(24px);
        cursor: default;
        padding: 0;
    }
}

.submenu__items {
    @include list-unstyled();

    @media (min-width: $break-header) {
        margin: 30px 0;
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        align-content: flex-start;
        height: 155px;

        .ua-ie-9 & {
            display: block;
            height: auto;
        }
    }
}

.submenu__item {
    @media (min-width: $break-header) {
        width: 280px;
        border-left: 1px solid $color-extra-2;
        padding-left: 20px;
        padding-bottom: 10px;

        .ua-ie-9 & {
            float: left;
        }
    }
}

.submenu__link {
    display: block;
    color: $color-base-1;
    font-size: 1.333rem;
    padding: 16px 30px 19px 30px;
    background-color: $color-grey-4;
    border-bottom: 1px solid $color-grey-3;

    @media (min-width: $break-header) {
        color: inherit;
        background: transparent;
        padding: 0;
        border-bottom: none;
        font-size: 1.111rem;

        &:hover {
            text-decoration: underline;
        }
    }
}

.submenu__container {
    padding: 20px;
    background-color: $color-grey-4;
    color: $color-grey-5;

    &--double {
        padding: 40px;
    }
}

.language-menu {
    position: relative;

    @media (min-width: $break-header) {
        list-style-type: none;
        padding-left: 0;
    }
}

.language-menu__button {
    background: none;
    border: none;
    color: inherit;
    padding: $menu-padding;
    padding-left: 25px;
    width: 100%;
    height: 70px;
    text-align: left;
    font-size: $font-size-h5;
}

.language-menu__items {
    @include list-unstyled;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100vw;
    display: none;
    background-color: $white;
    color: $color-base-1;
    z-index: 1;
    padding-left: 10px;
    box-shadow: 0 1px 5px 0 rgba(#a1a1a1, .8);

    .is-selected + & {
        display: block;
    }

    .vision__contrast & {
        border-bottom: 2px solid;

        @media (min-width: $break-header) {
            border-bottom: none;
        }
    }

}

.language-menu__item {
    &--selected {
        font-family: $font-lato;
        color: $color-grey-5;
        font-weight: 600;
    }

    @media (min-width: $break-header) {
        float: left;
        margin-left: .5em;
    }
}

.mobile-dropdown__trigger {
    &.is-selected {
        background-color: $color-grey-4;
        color: $color-base-1;
    }
}

.mobile-dropdown__container {
    display: none;
    position: absolute;
    background: $color-grey-4;
    left: 0;
    right: 0;
    height: 100vh;
    color: $color-base-1;
    padding: 10px;

    .is-selected + & {
        display: block;
    }

    &--compact {
        height: auto;
        box-shadow: 0 100vh 0 100vh rgba($color-grey-5, .95);
    }
}

.mobile-dropdown__icon {
    .is-selected &,
    &--selected {
        display: none;
    }

    .is-selected &--selected {
        display: inherit;
    }
}
