@import "sumosoelct";

//style default elements

select {
    .input & {
        display: block;
        border-radius: 4px;
        border: 1px solid $color-grey-3;
        text-indent: 10px;
        margin-top: 10px;
        width: 100%;
        background-color: $white;
        height: 40px;

        .placeholder {
            color: $color-grey-3;
        }
    }

    .input-inline & {
        display: inline;
        width: auto;
        margin-left: 10px;
        margin-right: 10px;
    }
}

.input {
    font-size: $font-size-h7;
    color: $color-grey-5;
    display: block;

    select:focus {
        box-shadow: 0 0 2px 2px rgba(199, 199, 199, .5);
    }

    .SumoSelect {
        display: block;
        width: auto;
        margin-top: 10px;

        &:focus {
            box-shadow: 0 0 2px 2px rgba(199, 199, 199, .5);
        }
    }

    .CaptionCont {
        border-color: $color-grey-3;
        border-radius: 4px;
        padding: 8px 15px 8px 14px;

        @at-root .is-focus#{&} {
            outline: none;
            //border-color: $color-focus;
            box-shadow: 0 0 2px 2px rgba(199, 199, 199, .5);
        }

        @at-root .is-invalid#{&} {
            border-color: $color-error;
        }
    }

    .optWrapper > .options li.opt {
        padding-left: 15px;
    }

    .SumoSelect > .CaptionCont > label > i {
        background: $color-base-1;
        mask-image: url("../svg/arrow-down.svg");
        mask-size: 100%;
    }

    .SumoSelect > .CaptionCont > span.placeholder {
        color: $color-grey-2;
        font-style: inherit;
    }

    .SumoSelect:focus > .CaptionCont,
    .SumoSelect:hover > .CaptionCont,
    .SumoSelect.open > .CaptionCont {
        box-shadow: 0 0 2px 2px rgba(199, 199, 199, .5);
        border-color: $color-grey-3;
    }

    &.input--no-label select {
        margin-top: 0;
    }
}
