@import "slick";

.gallery {
    background-color: $color-grey-1;
}

.gallery__nav {
    height: 20vh;
}

.gallery__slide {
    .gallery__slider & {
        max-height: 80vh;
        position: relative;
        display: flex !important;
        align-items: center;
        overflow: hidden;

        @media (min-width: $break-header) {
            padding-left: 60px;
            padding-right: 60px;
        }
    }

    .gallery__nav & {
        width: 100%;
        padding-top: 100%;
        box-sizing: content-box;
        overflow: hidden;
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;

        .slick-current & {
            border: 2px solid #ffffff;
        }
    }
}

.gallery__caption {
    min-height: 2em;
    color: #ffffff;
    font-size: ptr(14px);
    padding: 10px 5px 0 5px;

    .text-regular {
        padding-right: .3em !important;
    }
}

.gallery__figure {
    display: inline-block;
    margin: 60px auto 70px;
}

.gallery__image {
    .gallery__slider & {
        max-height: calc(70vh - 2em);
        max-width: 100%;
        box-shadow: 6px 6px 15px 1px rgba(0, 0, 0, .5);
    }

    .gallery__nav & {
        transform: translate(-50%, -50%);
        margin-left: 50%;
        margin-top: 50%;
    }
}

.slick-slide {
    .gallery__nav & {
        margin-right: 20px;
        width: 75px;

        @media (min-width: $break-header) {
            width: 80px;
        }
    }

    .gallery__nav &.slick-current {
        position: relative;
        top: -10px;

        .gallery__slide {
            border: 2px solid #ffffff;
        }
    }
}

.slick-track {
    .gallery__nav & {
        padding-top: 20px;
    }

    .gallery__slider & {
        display: flex;
        align-items: center;
    }
}

.gallery__arrow {
    position: absolute;
    top: 50%;
    z-index: 1;
    color: #ffffff;
    font-size: ptr(28px);
    background: none;
    border: none;

    &--right {
        right: 10px;
    }

    &--left {
        left: 10px;
    }
}

