.banner {
    width: 100%;
    overflow: hidden;
    position: relative;
    text-align: center;
    //max-height: 500px;
    max-height: 370px;

    &:before {
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        background-image: linear-gradient(to bottom, rgba($color-base-1, .1), rgba($white, 1));
    }

    &--no-content {
        margin-bottom: -40px;

        @media (min-width: $break-header) {
            margin-bottom: -112px;
        }
    }
}

.banner__background {
    max-width: 150%;
    margin-left: -25%;

    @media (min-width: $break-md) {
        width: auto;
        max-width: 100%;
        margin-left: 0;
    }
}

.banner__container {
    position: absolute;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    left: 0;
    right: 0;
    bottom: 0;
    text-align: center;
}

.banner__text {
    font-size: 1.5rem;
    text-align: center;

    @media (min-width: $break-sm) {
        font-size: 2.33rem;
    }

    @media (min-width: $break-md) {
        font-size: $font-size-h1;
    }
}
