.footer {
    color: $white;
    background-color: $color-base-2;
    padding-top: 40px;
    position: relative;

    @media (min-width: $break-header) {
        padding-top: 60px;
    }

    &:before {
        display: none;
        content: "";
        position: absolute;
        background-image: url(../svg/background-pattern-header.svg);
        background-repeat: no-repeat;
        background-size: 677px auto;
        background-position: right bottom;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        opacity: .04;

        @media (min-width: $break-header) {
            display: block;
        }
    }

    &--project {
        padding-top: 29px;
        padding-bottom: 29px;
        font-size: $font-size-h7;

        &:before {
            display: none;
        }

        .container {
            padding-left: 44px;
            padding-right: 44px;
        }
    }
}

.footer__block {
    padding-bottom: 10px;

    p {
        line-height: 1.222222;

        a {
            @include font-light;
            text-decoration: none;

            &:hover {
                text-decoration: underline;
            }
        }
    }

    .footer--project & {
        margin-right: 30px;

        @media (min-width: $break-header) {
            float: left;
            padding-bottom: 0;
        }
    }
}

.footer__top {
    position: relative;
    padding-bottom: 50px;

    @media (min-width: $break-header) {
        padding-bottom: 53px;
    }
}

.footer__bottom {
    position: relative;
    background-color: $color-base-1;
    padding: 17px;
    text-align: center;
}

.footer__link {
    padding-right: 1em;
    padding-left: 1em;
    @include font-light();
    color: $white;
    font-size: $font-size-h7;
    display: inline;

    @at-root a#{&}:hover {
        text-decoration: underline;
    }

    &:not(:first-child):before {
        border-left: 1px solid $white;
        content: "";
        top: 1px;
        left: -1em;
        position: relative;
        height: 12px;
        display: inline-block;
    }

    .footer--project & {
        padding: 0;
    }
}

.footer__button {
    border-color: $white;
    color: $white;

    &:hover {
        background-color: $white;
        color: $color-base-1;
        border-color: $white;
    }
}
