.map {
    position: relative;
    height: 100%;

    &--small {
        height: 200px;
    }

    &--full {
        height: 500px;
        width: 100vw;
        margin-left: 50%;
        transform: translateX(-50%);
        //margin-top: -80px;
        //z-index: -1;
    }

    .gmnoprint {
        pointer-events: none;
    }
}

.map__container {
    height: 100%;
}

.map__button {
    position: absolute;
    bottom: 40px;
    margin-left: 50%;
    transform: translateX(-50%);
    text-align: center;

    @media (min-width: $break-header) {
        text-align: right;
    }
}

.map__wrapper-link {
    width: 100%;
    height: 100%;
    display: inline-block;
}

.gm-style-cc {
    display: none;
}

