$active-sort: $color-grey-5;

@mixin direction {
    padding: 4px;
    border-radius: 1em;
    border: 1px solid $active-sort;
    color: $active-sort;
}

.sort-button {
    border: none;
    background: none;
    transform: translate(.3em, 0);
    padding: 0 5px;
    vertical-align: middle;
    color: $active-sort;

    &--desc {
        @include direction;

        .icon:first-child {
            color: $active-sort;
            display: none;
        }
    }

    &--asc {
        @include direction;

        .icon:last-child {
            color: $active-sort;
            display: none;
        }
    }
}

.sort-button__icon {
    display: block;
    font-size: ptr(10px);
}
