.modal-container {
    position: fixed;
    z-index: 30;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: none;
    overflow: auto;
    overflow-y: scroll;
    background: rgba($color-grey-1, .8);

    &--visible {
        display: flex;
        flex-direction: column;
    }

    .modal {
        display: block;
        pointer-events: normal;
    }
}

.modal-container__inner {
    width: 100%;
    margin: auto;
    align-self: center;
}

.modal {
    display: none;
    position: relative;
    padding: 30px 40px 40px 40px;
    background: $white;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .5);
    margin: 80px;
    max-width: $container-with;

    &--full {
        max-width: none;
        width: 100%;
        margin: 0;
        padding: 0;
    }

    &--dark {
        color: #ffffff;
    }

    @media only screen and (min-width: $break-sm) {
        //padding: 40px;
    }
}

.modal__full-width {
    margin-left: -20px;
    margin-right: -20px;

    @media only screen and (min-width: $break-sm) {
        margin-left: -40px;
        margin-right: -40px;
    }
}

.modal__close {
    position: absolute;
    top: 30px;
    right: 40px;
    background: transparent;
    border: 0;
    padding: 0;
    outline: none;
    color: #000000;
    font-size: ptr(16px);
    z-index: 1;

    .modal--dark & {
        color: #ffffff;
    }

    &:hover {
        color: $color-grey-4;
    }

    .icon {
        display: block;
    }
}

