.breadcrumb {
    @include list-unstyled();
    margin-left: 0 !important;
    //min-height: 1.5rem;
}

.breadcrumb__item {
    float: left;
    font-family: $font-lato !important;
    font-size: ptr(14px);
    font-weight: 400;
    color: $color-grey-1;

    /*    & + &:before {
            content: "/";
            padding-left: .2em;
            padding-right: .2em;
        }
        */

    &:after {
        content: "/";
        padding-left: .2em;
        padding-right: .2em;
    }

    &:last-child:after {
        content: unset;
    }

    &:first-child:after {
        content: "/";
    }

}

.breadcrumb__link {
    @include font-light();

    &:hover {
        text-decoration: underline;
    }
}
