$breake-image: 1440px;

.full-image {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;

    @media (max-width: $breake-image - 1) {
        background-image: none !important;
    }

}

.full-image__container {
    position: relative;
}

.full-image__label {
    z-index: 1;
}

.full-image__image {
    display: block;
    min-height: 100%;
    margin-left: 50%;
    margin-right: 50%;
    transform: translateX(-50%);

    @media (min-width: $breake-image) {
        visibility: hidden;
    }
}

.full-image__button {
    position: absolute;
    z-index: 1;
    bottom: 20px;
    margin-left: 50%;
    transform: translateX(-50%);
    left: 0;
    box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, .5);
    background-color: $white;

    @media (min-width: $break-header) {
        right: 40px;
        top: 30px;
        bottom: unset;
        left: unset;
        transform: unset;
        margin-left: unset;
    }
}
