[data-toggle="collapse"] {
    cursor: pointer;
}

.collapse {
    display: none;
    position: relative;

    &--visible,
    &.is-visible {
        display: block;
    }

    &--collapsing,
    &.is-collapsing {
        position: relative;
        height: 0;
        overflow: hidden;
        transition: height .3s ease-in-out;
    }
}

.collapse__container {
    background: $white;
    //box-shadow: 0 1px 1px rgba($black, .1);
    padding: 40px 20px 20px;
    margin-top: 10px;

    @media (min-width: $break-sm) {
        padding: 40px;
    }

    &--no-shadow {
        box-shadow: none;
    }
}

