.collapse__close {
    display: none;
    position: absolute;
    z-index: 1;
    color: $color-grey-5;
    font-size: ptr(16px);
    top: 1em;
    right: 1em;
    border: none;

    &:hover {
        //color: $collapse-close-hover;
    }

    .collapse--visible &,
    .collapse.is-visible & {
        display: block;
    }
}
