.dropdown {
  position: relative;
}

.dropdown__trigger {
  color: inherit;
}

.dropdown__trigger:focus {
  outline: 0;
}

.dropdown.open .dropdown__trigger {
  color: inherit;
  outline: 0;
}

.dropdown__menu {
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 1000;
  display: none;
  float: left;
  min-width: 100%;
  padding: 5px 0;
  margin: 2px 0 0;
  list-style-type: none;
  padding-left: 0;
  text-align: left;
  padding-top: 1em;
  padding-bottom: 1em;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
  background-color: #ffffff;
  /* Backward compability */
  /* Backward compability end */
}

.dropdown--grey .dropdown__menu {
  background: #00ff00;
  padding: 0;
  /* Backward compability */
  /* Backward compability end */
}

.dropdown--grey .dropdown__menu > li > a {
  padding: 8px 14px;
}

.dropdown--grey .dropdown__menu > li > a:hover, .dropdown--grey .dropdown__menu > li > a:focus {
  background: #ffffff;
  color: #000000;
}

.dropdown--white .dropdown__menu {
  background: #ffffff;
  padding: 0;
  /* Backward compability */
  /* Backward compability end */
}

.dropdown--white .dropdown__menu > li > a {
  padding: 8px 14px;
}

.dropdown--white .dropdown__menu > li > a:hover, .dropdown--white .dropdown__menu > li > a:focus {
  background: #00ff00;
  color: #000000;
}

.dropdown.open .dropdown__menu {
  display: block;
}

.dropdown.dropup .dropdown__menu {
  top: auto;
  bottom: 100%;
  margin-bottom: -32px;
}

.dropdown__menu > li > a {
  display: block;
  padding: 6px 14px;
  clear: both;
  font-weight: 400;
  color: #000000;
  white-space: nowrap;
}

.dropdown__menu > li > a:hover, .dropdown__menu > li > a:focus {
  text-decoration: none;
  color: #000000;
}

.dropdown__menu > .active > a, .dropdown__menu > .active > a:hover, .dropdown__menu > .active > a:focus {
  color: #000000;
  text-decoration: none;
  outline: 0;
}

.dropdown__menu > .disabled > a, .dropdown__menu > .disabled > a:hover, .dropdown__menu > .disabled > a:focus {
  color: #828282;
}

.dropdown__menu > .disabled > a:hover, .dropdown__menu > .disabled > a:focus {
  text-decoration: none;
  background-color: transparent;
  background-image: none;
  cursor: disabled;
}

.dropdown__menu--right {
  left: auto;
  right: 0;
}

.dropdown__menu--left {
  left: 0;
  right: auto;
}

.dropdown__menu-link {
  display: block;
  padding: 6px 14px;
  clear: both;
  font-weight: 400;
  color: #000000;
  white-space: nowrap;
}

.dropdown__menu-link:hover, .dropdown__menu-link:focus {
  text-decoration: none;
  color: #000000;
}

.dropdown__menu > .active > .dropdown__menu-link, .dropdown__menu > .active > .dropdown__menu-link:hover, .dropdown__menu > .active > .dropdown__menu-link:focus {
  color: #000000;
  text-decoration: none;
  outline: 0;
}

.dropdown__menu > .disabled > .dropdown__menu-link, .dropdown__menu > .disabled > .dropdown__menu-link:hover, .dropdown__menu > .disabled > .dropdown__menu-link:focus {
  color: #828282;
}

.dropdown__menu > .disabled > .dropdown__menu-link:hover, .dropdown__menu > .disabled > .dropdown__menu-link:focus {
  text-decoration: none;
  background-color: transparent;
  background-image: none;
  cursor: disabled;
}

.dropdown--grey .dropdown__menu-link {
  padding: 8px 14px;
}

.dropdown--grey .dropdown__menu-link:hover, .dropdown--grey .dropdown__menu-link:focus {
  background: #ffffff;
  color: #000000;
}

.dropdown--white .dropdown__menu-link {
  padding: 8px 14px;
}

.dropdown--white .dropdown__menu-link:hover, .dropdown--white .dropdown__menu-link:focus {
  background: #00ff00;
  color: #000000;
}

.dropdown.open .dropdown__menu-link {
  outline: 0;
}

.dropdown__icon {
  font-size: .833rem;
  transition: transform .3s ease-in-out;
  margin-top: .2em;
  margin-left: 4px;
  position: relative;
  top: 3px;
}

.dropdown.open .dropdown__icon {
  transform: rotate(180deg);
}

.dropdown__header {
  display: block;
  font-size: 0.77778rem;
  line-height: 1;
  color: #828282;
  white-space: nowrap;
}

.dropdown__backdrop {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  z-index: 999;
}

[data-toggle="close-dropdown"] {
  position: absolute;
  right: 16px;
  top: 16px;
  font-size: 1rem;
}
