$radio-background: $white;
$radio-border: 1px solid $color-grey-3;
$radioed-color: $color-base-2;
$radio-indicator-color: $color-base-2;
$radio-icon-position: 4px;

.radio {
    position: relative;
    display: flex;
    align-items: center;
    padding-left: 25px;
    min-height: 1.5em;
    cursor: pointer;
    text-align: left;

    .radio__input:disabled ~ & {
        cursor: default;
    }

    & + & {
        margin-top: 20px;
    }
}

.radio__input {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
}

.radio__text {
    font-size: $font-size-h7;
    font-weight: 400;
    font-family: $font-lato;
    padding-left: 10px;
    color: $color-grey-5;

    //.radio__input:disabled ~ .radio__label & {
    //    color: $grey-d;
    //}

    .radio--text-normal & {
        font-size: inherit;
        font-weight: inherit;
    }

    .radio--light & {
        color: $white;
    }
}

.radio__indicator {
    display: block;
    border: $radio-border;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background: $radio-background;
    color: $radio-indicator-color;
    position: absolute;
    top: 0;
    left: 0;

    &:before {
        content: "";
        display: block;
        width: 17px;
        height: 17px;
        background: currentColor;
        border-radius: 50%;
        position: absolute;
        top: 3px;
        left: 3px;
        opacity: 0;

        .radio__input:checked ~ & {
            opacity: 1;
        }
    }
}
