$black: #000000;
$grey-m: #00ff00;
$text-color-base: #e49233;
$dropdown-selected-color: #000000;
$grey-d: #828282;
$base-line-height: 1;

.dropdown {
    position: relative;
}

.dropdown__trigger {
    color: inherit;

    &:focus {
        outline: 0;
    }

    .dropdown.open & {
        color: inherit;
        outline: 0;
    }
}

.dropdown__menu {
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 1000;
    display: none;
    float: left;
    min-width: 100%;
    padding: 5px 0;
    margin: 2px 0 0;
    @include list-unstyled();
    text-align: left;
    padding-top: 1em;
    padding-bottom: 1em;
    box-shadow: 0 1px 1px rgba($black, .2);
    background-color: $white;

    .dropdown--grey & {
        background: $grey-m;
        padding: 0;

        /* Backward compability */

        > li > a {
            padding: 8px 14px;

            &:hover,
            &:focus {
                background: $white;
                color: $black;
            }
        }

        /* Backward compability end */
    }

    .dropdown--white & {
        background: $white;
        padding: 0;

        /* Backward compability */

        > li > a {
            padding: 8px 14px;

            &:hover,
            &:focus {
                background: $grey-m;
                color: $black;
            }
        }

        /* Backward compability end */
    }

    .dropdown.open & {
        display: block;
    }

    // Allow for dropdowns to go bottom up (aka, dropup-menu)

    .dropdown.dropup & {
        top: auto;
        bottom: 100%;
        margin-bottom: -32px;
    }

    /* Backward compability */

    > li > a {
        display: block;
        padding: 6px 14px;
        clear: both;
        font-weight: 400;
        color: $black;
        white-space: nowrap;

        &:hover,
        &:focus {
            text-decoration: none;
            color: $dropdown-selected-color;
        }
    }

    > .active > a {
        &,
        &:hover,
        &:focus {
            color: $dropdown-selected-color;
            text-decoration: none;
            outline: 0;
        }
    }

    > .disabled > a {
        &,
        &:hover,
        &:focus {
            color: $grey-d;
        }

        &:hover,
        &:focus {
            text-decoration: none;
            background-color: transparent;
            background-image: none;
            cursor: disabled;
        }
    }

    /* Backward compability end */
}

.dropdown__menu--right {
    left: auto;
    right: 0;
}

.dropdown__menu--left {
    left: 0;
    right: auto;
}

.dropdown__menu-link {
    display: block;
    padding: 6px 14px;
    clear: both;
    font-weight: 400;
    color: $black;
    white-space: nowrap;

    &:hover,
    &:focus {
        text-decoration: none;
        color: $dropdown-selected-color;
    }

    // Active state

    .dropdown__menu > .active > & {
        &,
        &:hover,
        &:focus {
            color: $dropdown-selected-color;
            text-decoration: none;
            outline: 0;
        }
    }

    // Disabled state

    .dropdown__menu > .disabled > & {
        &,
        &:hover,
        &:focus {
            color: $grey-d;
        }

        &:hover,
        &:focus {
            text-decoration: none;
            background-color: transparent;
            background-image: none;
            cursor: disabled;
        }
    }

    .dropdown--grey & {
        padding: 8px 14px;

        &:hover,
        &:focus {
            background: $white;
            color: $black;
        }
    }

    .dropdown--white & {
        padding: 8px 14px;

        &:hover,
        &:focus {
            background: $grey-m;
            color: $black;
        }
    }

    .dropdown.open & {
        outline: 0;
    }
}

.dropdown__icon {
    font-size: .833rem;
    transition: transform .3s ease-in-out;
    margin-top: .2em;
    margin-left: 4px;
    position: relative;
    top: 3px;

    .dropdown.open & {
        transform: rotate(180deg);
    }
}

// Dropdown section headers

.dropdown__header {
    display: block;
    font-size: $font-size-small;
    line-height: $base-line-height;
    color: $grey-d;
    white-space: nowrap;
}

// Backdrop to catch body clicks on mobile, etc.

.dropdown__backdrop {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    z-index: 999;
}

[data-toggle="close-dropdown"] {
    position: absolute;
    right: 16px;
    top: 16px;
    font-size: ptr(18px);
}
