@mixin header-variant($background-color, $text-color, $accent-color: $text-color) {
    background-color: $background-color;
    color: $text-color;

    .header-project__logo {
        &.icon {
            font-size: 4.8888rem;
            height: .2em;
            top: -15px;
            position: relative;
            margin-bottom: -.2em;
        }
    }

    .header-project__section {
        float: left;
        display: block;

        &--middle {
            margin-top: 17px;
        }
    }

    .header-project__logo-section {
        display: inline-block;
        width: 232px;
    }

    .header-project__user-section {
        display: inline-block;
        float: right;
    }

    .header-project__add {
        position: absolute;
        background-color: $background-color;
        font-size: ptr(20px);
        border-bottom-left-radius: 1em;
        border-bottom-right-radius: 1em;
        height: 50px;
        text-align: center;
        box-shadow: 0 2px 4px 0 rgba(74, 74, 74, .5);
        padding-left: 30px;
        padding-right: 30px;
    }

    .header-project__add-link {
        display: block;
        line-height: 48px;
        font-size: ptr(20px);
        font-weight: 400;
    }

    .header-project__dropdown {
        display: inline-block;
        margin-top: -3px;

        .dropdown__trigger {
            line-height: 1;
            font-size: $font-size-h7;
            padding-top: 8px;

            &:hover {
                text-decoration: none;
            }
        }

        .dropdown__menu {
            padding: 0;
        }

        .dropdown__menu-item {
            &:not(:last-child) {
                border-bottom: 1px solid $color-grey-3;
            }

            &:hover {
                background-color: $color-grey-4;

                .dropdown__menu-link {
                    @include font-regular();
                    font-weight: 600;
                    color: $color-base-1;
                }
            }
        }

        .dropdown__menu-link {
            @include font-light;
            color: $color-base-1;
            font-size: $font-size-h7;
            padding: 14px 20px;
        }

        &--user {
            padding-left: 20px;
            padding-right: 20px;
            margin-right: -20px;
        }
    }

    .header-project__dropdown-text {
        @include font-light();
        position: relative;

        &:before {
            content: "";
            width: 3px;
            height: 3px;
            background-color: $text-color;
            display: inline-block;
            margin: .2em 4px .2em 2px;
        }
    }

    .header-project__search {
        display: flex;
        float: left;

        input {
            border: none;
            border-top-left-radius: 2rem;
            border-bottom-left-radius: 2rem;
            background-color: rgba($text-color, .15);
            padding: 6px 15px 7px 15px;
            width: 155px;

            &::placeholder {
                color: $text-color;
            }

            &:focus {
                outline: none;
            }
        }

        button {
            line-height: 1;
            border: 1px solid transparent;
            background-color: $text-color;
            color: $color-base-1;
            padding: 10px 8px 9px 8px;
            width: 45px;
            text-align: center;
            font-size: .8333rem;
            border-top-right-radius: 2rem;
            border-bottom-right-radius: 2rem;

            .icon {
                margin-bottom: -2px;
            }

            &:focus {
                outline: none;
            }

            &:hover {
                border-color: $white;
                background-color: rgba($text-color, .15);
                color: $white;
            }
        }
    }

    .header-project__search-detail {
        @include font-light();
        margin-left: 10px;
        display: inline-block;
        margin-top: 7px;
        font-size: $font-size-h7;

        &:hover {
            text-decoration: underline;
        }
    }

    .project-menu {
        @include list-unstyled;

        display: inline-block;

        li {
            float: left;
        }
    }

    .project-menu__item {
        height: 70px;
        line-height: 70px;
        margin-left: 30px;

        &:first-child {
            margin-left: 0;
        }

        &:hover,
        &.is-selected {
            border-bottom: 3px solid $accent-color;
            color: $accent-color;
        }
    }

    .project-menu__badge {
        font-size: ptr(12px);
        border-radius: 12px;
        height: 16px;
        min-width: 10px;
        text-align: center;
        display: inline-block;
        background-color: $accent-color;
        line-height: 16px;
        box-sizing: content-box;
        position: relative;
        top: -2px;
        padding-left: 3px;
        padding-right: 3px;
        color: $text-color;
    }

    .project-menu__link {
        display: block;
        font-size: $font-size-h7;
        font-weight: 400;
    }

    .header-project__notice {
        position: absolute;
        left: 0;
        right: 0;
        z-index: 10;
    }
}

.header-project {
    //background-color: $background-color;
    //color: $text-color;
    margin-bottom: 50px;
    position: fixed;
    width: 100%;
    z-index: 999;

    @include header-variant($color-base-3, $white);

    &--dhs {
        @include header-variant($color-base-1, $white, $color-extra-2);
    }
}
